<template>
	<section id="game-card"> 
    <div class="col">
      <div class="main-con">

			<div class="card px-md-4 px-sm-3 pt-4 pb-2 mt-3 inner-card"> 
				<div class="mb-3 d-flex align-items-start"> 
					<img src="/img/1.png" class="mr-md-5 mr-sm-3">  
					<p class="success-text">{{detail.name}}</p> 
				</div>
				<div class="row border-top">
				</div>
				<div class="row mt-2">
				</div>
				<div class="row justify-content-center align-items-center">
					<p class="time" :id="detail.id">Closed</p>
				</div> 
				<div class="row pl-3 mt-2 border-bottom justify-content-center align-items-center" v-if="!detail.available">
					<p class="time-count mr-4">DAYS</p> <br />
					<p class="time-count mr-4">HOURS</p>
					<p class="time-count">MINS</p>
					<!-- <p class="time-count">SECS</p> -->
				</div>
					<router-link class="px-md-3 mb-3 btn button d-flex justify-content-center mt-3" to="/result">VIEW RESULT</router-link>    
			</div>
		</div>  
		
    </div>
    
		<!--Run v-for="(detail, index) in details" :key="index" on you page and pass the required data to utilize component-->
		
	</section>
</template>

<script>
import moment from 'moment';
export default {
  props: { 
    detail: Object
	},
	mounted () {
  	this.prepareCountDown()
	},
	//class="col-4" style="padding:5px;"
	methods: {
		getLastDraw :  function(date) {
				const mode = this.detail.name == 'Winna' || this.detail.name == 'Cash' || this.detail.name == 'Millions' ? 7 : 1; 
				const yesterday = moment(date).subtract(mode, 'days').format('YYYY-MM-DD HH:MM');
				return yesterday;
		},
		prepareCountDown : function() {
			// time = this.detail.date;
			let self = this;
			if(!self.detail.available) {
				var time = self.detail.date;
				var countDownDate = new Date(time).getTime();
				
				// Update the count down every 1 second
				var x = setInterval(function () {

					// Get todays date and time
					var now = new Date().getTime();
				
					// Find the distance between now and the count down date
					var distance = countDownDate - now;
					// Time calculations for days, hours, minutes and seconds
					var days = Math.floor(distance / (1000 * 60 * 60 * 24));
					var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
					var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
					// var seconds = Math.floor((distance % (1000 * 60)) / 1000);
				
					// Display the result in the element with id="demo"
					document.getElementById(self.detail.id).innerText = days + "d " + hours + "h "
					+ minutes + "m";
				
					// If the count down is finished, write some text
					if (distance < 0) {
						clearInterval(x);
						document.getElementById(self.detail.id).innerHTML = "EXPIRED";
					}
				}, 1000);
			}
			else {
				document.getElementById(self.detail.id).innerText = "Closed";
			}
		}
	}
}
</script>

